import React from "react";
import Row from "muicss/lib/react/row";
import { connect } from "react-redux";
import * as qtsActions from "../../actions/qtsActions";
import { bindActionCreators } from "redux";
import QTSRates from "./QTSRates";
import Modal from "react-modal";
import Expire from "../common/Expire";
import Button from "muicss/lib/react/button";
import getParameterByName from "../common/getParameterByName";
import AskQTSSearchResults from "./AskQtsSearchResults";

class QTSRatesResults extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      isOpened: false,
      modalOpen: false,
      contact: {},
      brokerName: "",
      brokerId: "",
      searchParams: {
        latitude: null,
        longitude: null,
        state: null,
        radius: null,
        property_types: [],
        min_loan: null,
        max_loan: null,
        date_range: null,
        bank: null,
        banker: null,
        lender_types: [],
        note_search: null,
        search_type: null,
        in_nyc: null,
        page: 0,
      },
    };
    this.afterOpenModal = this.afterOpenModal.bind(this);
    this.closeModal = this.closeModal.bind(this);
    this.getContactFullInfo = this.getContactFullInfo.bind(this);
    this.openContactModal = this.openContactModal.bind(this);
  }

  UNSAFE_componentWillMount() {
    this.setState(
      {
        searchParams: {
          latitude: getParameterByName("latitude"),
          longitude: getParameterByName("longitude"),
          state: getParameterByName("state"),
          city: getParameterByName("city"),
          radius: getParameterByName("radius"),
          property_types: getParameterByName("property_types")
            ? decodeURIComponent(getParameterByName("property_types"))
                .split(",")
                .map((p) => parseInt(p, 10))
            : [],
          min_loan: getParameterByName("min_loan")
            ? parseInt(getParameterByName("min_loan").replace(/\D/g, ""), 10)
            : null,
          max_loan: getParameterByName("max_loan")
            ? parseInt(getParameterByName("max_loan").replace(/\D/g, ""), 10)
            : null,
          date_range: getParameterByName("date_range")
            ? parseInt(getParameterByName("date_range"), 10)
            : null,
          bank: getParameterByName("bank"),
          banker: getParameterByName("banker"),
          lender_types: getParameterByName("lending_types")
            ? decodeURIComponent(getParameterByName("lending_types"))
                .split(",")
                .map((p) => parseInt(p, 10))
            : [],
          note_search: getParameterByName("note_search"),
          search_type: getParameterByName("search_type"),
          in_nyc: getParameterByName("in_nyc")
            ? parseInt(getParameterByName("in_nyc"), 10)
            : null,
          page: getParameterByName("page") || 1,
        },
      },
      () => {
        let searchParams = this.state.searchParams;
        Object.keys(searchParams).forEach(
          (key) =>
            (searchParams[key] === null || searchParams[key] === "") &&
            delete searchParams[key]
        );
        this.props.qtsActions.loadQTSRatesResults(searchParams);
      }
    );
  }

  UNSAFE_componentWillReceiveProps(nextProps) {
    if (this.props.location.search !== nextProps.location.search) {
      this.UNSAFE_componentWillMount();
    }
  }




  getContactFullInfo(contact_id) {
    this.props.qtsActions.getFullContactInfo(contact_id);
  }

  openContactModal(brokerId, brokerName) {
    this.getContactFullInfo(brokerId);
    this.setState({
      modalOpen: true,
      brokerName: brokerName,
      brokerId: brokerId,
    });
  }

  afterOpenModal() {}

  closeModal() {
    this.setState({
      modalOpen: false,
    });
  }

  render() {
    let qtsRates = this.props.qts.qtsRates;

    let toast = null;
    if (this.props.qts.toast.show) {
      toast = (
        <Expire closeToast={this.props.qtsActions.closeToast} delay={6000}>
          <div className="success-message">
            <p>{this.props.qts.toast.message}</p>
            <Button variant="flat" onClick={this.props.qtsActions.closeToast}>
              {this.props.qts.toast.success ? "Done" : "Dismiss"}
            </Button>
          </div>
        </Expire>
      );
    }
    return (
      <div>
        {!this.props.qts.loadingRates ? (
          <AskQTSSearchResults>
            <Row>
              <div className="qts-results">
                {toast}
                {qtsRates && qtsRates.length ? (
                  <QTSRates
                    rates={qtsRates}
                  />
                ) : (
                  <div className={this.props.qts.noRates ? "" : "hidden"}>
                    <div>
                      <div className="grey-circle">
                        <i className="material-icons">warning</i>
                      </div>
                      <h2 className="no-results">No Rates Results Found</h2>
                    </div>
                  </div>
                )}
              </div>
            </Row>
          </AskQTSSearchResults>
        ) : (
          <Modal
            className="loading-modal"
            isOpen={true}
            ariaHideApp={false}
            contentLabel="Modal"
            shouldCloseOnOverlayClick={false}
          >
            <div className="centered-spinner">
              <img
                src="/images/icons/spin.svg"
                className="loading-spinner"
                alt="loading spinner"
              />
            </div>
          </Modal>
        )}
      </div>
    );
  }
}

function mapStateToProps(state) {
  return {
    qts: state.QTS,
  };
}

function mapDispatchToProps(dispatch) {
  return {
    qtsActions: bindActionCreators(qtsActions, dispatch),
  };
}

export default connect(mapStateToProps, mapDispatchToProps)(QTSRatesResults);
