import React from "react";
import Row from "muicss/lib/react/row";
import Col from "muicss/lib/react/col";
import getParameterByName from "../common/getParameterByName";
const QTSRates = (props) => {
  const rates = props.rates;
  const AGENCY5 = rates.filter(i => (i.LENDING_PROGRAM === "Agency" && i.TERM === 5))
    .map(lp => { return ({MIN_RATE : lp.MIN_RATE, MAX_RATE: lp.MAX_RATE}) })[0];
  const AGENCY10 = rates.filter(i => (i.LENDING_PROGRAM === "Agency" && i.TERM === 10))
    .map(lp => { return ({MIN_RATE : lp.MIN_RATE, MAX_RATE: lp.MAX_RATE}) })[0];
  const CMBS5 = rates.filter(i => (i.LENDING_PROGRAM === "CMBS" && i.TERM === 5))
    .map(lp => { return ({MIN_RATE : lp.MIN_RATE, MAX_RATE: lp.MAX_RATE}) })[0];
  const CMBS10 = rates.filter(i => (i.LENDING_PROGRAM === "CMBS" && i.TERM === 10))
    .map(lp => { return ({MIN_RATE : lp.MIN_RATE, MAX_RATE: lp.MAX_RATE}) })[0];

  const nonAgency = rates.filter(i => (i.LENDING_PROGRAM !== "Agency" && i.LENDING_PROGRAM !== "CMBS" ));
  const nonAgencyTotalQuotes = nonAgency.reduce((acc, lp) => {return acc + lp.TOTAL_QUOTES}, 0);

  const nonAgencyLendersArrays = nonAgency.map(lp => lp.LENDERS.split(","));
  const nonAgencyLendersDups  = [].concat.apply([], nonAgencyLendersArrays);
  const nonAgencyTotalLenders = [...new Set(nonAgencyLendersDups)].length;

 const nonAgencySections = [
   {section : "Multi-Family", data :  nonAgency.filter(i => i.ASSET_TYPE === "Multi-Family")},
   {section : 'Mixed-use', data :  nonAgency.filter(i => i.ASSET_TYPE === 'Mixed-use')},
   {section : 'Retail / Shopping', data :  nonAgency.filter(i => i.ASSET_TYPE === 'Retail / Shopping')},
   {section : 'Office', data :  nonAgency.filter(i => i.ASSET_TYPE === 'Office Building')},
   {section : 'Industrial', data :  nonAgency.filter(i => i.ASSET_TYPE === 'Industrial')}
 ];

  console.log("AGENCY5", AGENCY5);
  console.log("AGENCY10", AGENCY10);
  console.log("CMBS5", CMBS5);
  console.log("CMBS10", CMBS10);
  console.log("nonAgencyTotalQuotes", nonAgencyTotalQuotes);
  console.log("nonAgencyTotalLenders", nonAgencyTotalLenders);
  console.log("nonAgencySections", nonAgencySections);

  return (
    <div>
      <div className="qts-rates panel mui--z1">
        <Row
          className="panel-heading"
        >
          <Col md="12" sm="12">
            <div className="title">
              Based on quotes brokers at {/* GPARENCY */} got in general for multi family is:
            </div>
          </Col>
        </Row>
        { AGENCY5 &&
        <Row>
          <Col xl="6" lg="6" md="6" sm="12" className="details-info flex-column">
            <p className="header">(Nationwide Agency quotes) Agency 5 year range:</p>
          </Col>
          <Col xl="6" lg="6" md="6" sm="12" className="details-info flex-column">
            <p>{AGENCY5.MIN_RATE}% -{AGENCY5.MAX_RATE}%</p>
          </Col>
        </Row>
        }
        { AGENCY10 &&
        <Row>
          <Col xl="6" lg="6" md="6" sm="12" className="details-info flex-column">
            <p className="header">(Nationwide Agency quotes) Agency 10 year range:</p>
          </Col>
          <Col xl="6" lg="6" md="6" sm="12" className="details-info flex-column">
            <p>{AGENCY10.MIN_RATE}% -{AGENCY10.MAX_RATE}%</p>
          </Col>
        </Row>
        }
        { CMBS5 &&
        <Row>
          <Col xl="6" lg="6" md="6" sm="12" className="details-info flex-column">
            <p className="header">(Nationwide CMBS quotes) CMBS 5 year spread range:</p>
          </Col>
          <Col xl="6" lg="6" md="6" sm="12" className="details-info flex-column">
            <p>{CMBS5.MIN_RATE}% -{CMBS5.MAX_RATE}%</p>
          </Col>
        </Row>
        }
        { CMBS10 &&
        <Row>
          <Col xl="6" lg="6" md="6" sm="12" className="details-info flex-column">
            <p className="header">(Nationwide CMBS quotes) CMBS 5 year spread range:</p>
          </Col>
          <Col xl="6" lg="6" md="6" sm="12" className="details-info flex-column">
            <p>{CMBS10.MIN_RATE}% -{CMBS10.MAX_RATE}%</p>
          </Col>
        </Row>
        }
      </div>
      <div className="qts-rates panel mui--z1">
        <Row
          className="panel-heading"
        >
          <Col md="12" sm="12">
            <div className="title">
              {`In the ${getParameterByName("state")} market for multi family we recently got
              ${nonAgencyTotalQuotes} quotes from ${nonAgencyTotalLenders} different lenders:`}
            </div>
          </Col>
        </Row>
        {
          nonAgencySections.map((section,sIndex) => {
            const conventional = ["Multi-Family", "Retail / Shopping", "Mixed-use"]
                                 .includes(section.section) ? "Conventional" : "";
            return (
              section.data &&
              <div key={sIndex} className="qts-rates panel mui--z1">
                {
                  section.data.map((item,iIndex) => {
                    return (
                      <Row className="nonAgency"  key={iIndex}>
                        <Col xl="6" lg="6" md="6" sm="12" className="details-info flex-column">
                          <p className="header">{section.section} {conventional} {item.TERM} Year Range:</p>
                        </Col>
                        <Col xl="6" lg="6" md="6" sm="12" className="details-info flex-column">
                          <p> {item.MIN_RATE}% - {item.MAX_RATE}% </p>
                        </Col>
                      </Row>
                    )
                  })
                }
              </div>
            )
          })
        }
      </div>
    </div>
  );
};

export default QTSRates;
